import { getDestinationById } from '@/data/data';
import { getAllDestination } from '@/helper/data-util';
import { useRouter } from 'next/router'
import React from 'react'

function TripDetailPage(props) {
  const destinationDetails = props.DestinationDetails;

  if(!destinationDetails){
    return <p>Loading...</p>
  }


  return (
    <main className='mt-50 max-w-7xl mx-auto mb-20'>
      <h1 className='text-4xl font-bold px-5 mb-5'>Trip Detail Page - {destinationDetails.Id}</h1>
      <p className='text-xl font-medium'>
        This is the detail page for trip with ID: {destinationDetails.Id}
      </p>
    </main>
  )
}


export async function getStaticprops(context){
  const {params} =context;
  const {Id} = params;

  const destiantion = await getDestinationById(Id)

  return{
    props: {
      DestinationDetails: destiantion
    },
    revalidate: 3600
  }
}

export async function getStaticpaths() {
  const destinations = await getAllDestination();

  const paths = destinations.map((destination) => ({params: {Id:destination.Id}}));

  return {
    paths: paths,
    fallback: true
  }
}



export default TripDetailPage